@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff"), local("Oswald-Regular");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff"), local("Oswald-Medium");
  font-style: normal;
  font-weight: 700;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-color: #ffffff;
  color: #444444;
}
a {
  text-decoration: none;
  color: #444444;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 320px;
  margin: 0 auto;
  margin-top: 13px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}
.page-header__logo {
  padding-left: 20px;
  margin-right: 80px;
}
.page-header__logo:hover,
.page-header__logo:focus {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .page-header {
    width: auto;
    margin-top: 24px;
    padding-left: 30px;
    padding-right: 30px;
    flex-direction: row;
    justify-content: space-between;
  }
  .page-header__logo {
    margin: 0;
    padding-left: 0;
    width: 174px;
  }
}
@media (min-width: 1300px) {
  .page-header {
    max-width: 1300px;
    margin-top: 50px;
    padding-left: 40px;
    padding-right: 40px;
  }
  .page-header__logo {
    margin: 0;
    padding: 0;
    width: 202px;
  }
}
.main-nav {
  position: relative;
}
.main-nav__wrap {
  overflow: hidden;
}
.main-nav__toggle {
  position: absolute;
  top: -44px;
  right: 0px;
  width: 72px;
  height: 42px;
  border: none;
  background: none;
}
.main-nav__toggle-icon {
  fill: #000000;
}
.main-nav__toggle:hover + .main-nav__wrap .main-nav__list {
  height: 213px;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .main-nav__wrap {
    margin: 0;
    overflow: visible;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1300px) {
  .main-nav__wrap {
    margin: 0;
    overflow: visible;
  }
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 0;
  transform: translateY(-100%);
  transition: all 0.3s linear;
}
.site-list__item a {
  border-top: 2px solid #e5e5e5;
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  display: block;
  padding-top: 24px;
  padding-bottom: 24px;
}
.site-list__item a[href]:hover,
.site-list__item a[href]:focus {
  color: rgba(17, 17, 17, 0.6);
}
.site-list__item a[href]:active {
  color: rgba(17, 17, 17, 0.3);
}
.site-list__item:last-child a {
  border-bottom: 2px solid #e5e5e5;
}
.site-list:hover {
  height: 213px;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .site-list {
    transform: translateY(0);
    height: auto;
    flex-direction: row;
  }
  .site-list:hover {
    height: auto;
  }
  .site-list__item {
    margin-right: 30px;
  }
  .site-list__item:last-child {
    margin-right: 0;
  }
  .site-list__item a {
    padding-top: 18px;
    padding-bottom: 8px;
    font-size: 18px;
    line-height: 25px;
    border: none;
  }
  .site-list__item--active {
    border-bottom: 2px solid #68b738;
  }
  .site-list__item:last-child a {
    border-bottom: none;
  }
}
@media (min-width: 1300px) {
  .site-list__item {
    margin-right: 38px;
  }
  .site-list__item a {
    color: #ffffff;
    font-size: 20px;
  }
  .site-list__item--active {
    color: #ffffff;
    border-bottom: 2px solid #ffffff;
  }
  .site-list__item-inner a {
    color: #000000;
  }
  .site-list__item-inner--active {
    border-bottom: 2px solid #68b738;
  }
}
